.forgotp-container {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f2f2f2;
}

.forgotp-container h2 {
  margin-top: 0;
}

.forgotp-container form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.forgotp-container label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

.forgotp-container input[type="email"] {
  padding: 10px;
  border-radius: 5px;
  border: none;
  margin-bottom: 20px;
  width: 100%;
}

.forgotp-container button[type="submit"] {
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  background-color: #008CBA;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

.forgotp-container button[type="submit"]:hover {
  background-color: #005b84;
}

.forgotp-container button {
  background-color: transparent;
  border: none;
  color: #008CBA;
  text-decoration: underline;
  font-size: 16px;
  cursor: pointer;
  margin-top: 20px;
}

.forgotp-container button:hover {
  color: #005b84;
}
